    bottom: 10%;
    right: 13%;
    
    
} */

.a-card {
  width: 60%;
  height: 70vh;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px 3px purple;
  background-image: url("./me3.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.a-title {
  font-weight: 800;
  font-size: 40px;
  margin-top: 100px;

  width: 80%;
}
.a-sub {
  margin: 20px 0px;
}
.a-desc {
  font-size: 30px;

  letter-spacing: 2px;
  line-height: 40px;
  margin-top: 50px;
  width: 80%;
}
.a-shortdesc {
  display: none;
}
