.a-short-navbar {
  margin: 10px 0px;
  list-style: none;
  display: flex;
  color: purple;
}
.a-short-navbar li {
  margin-right: 20px;
  padding: 5px 0px;
}
.bottom-border {
  border-bottom: 2px solid red;
}
@media (width<=500px) {
  .a {
    flex-direction: column;
    justify-content: space-around;
    
  
    margin: 30px 0;
  }
  .a-left {
    width: 100%;
    
  }
  .a-card {
    height: 30vh;
  }
  .a-card.bg,
  .a-award {
    display: none;
  }
  .a-right {
    width: 100%;
    
   

    
  }
  .a-desc {
    display: none;
  }
  .a-title{
    margin-top:20px ;
    
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
  }
  .a-shortdesc {
    display: block;
    font-size: 30px;

    
    line-height: 35px;
    margin-top: 20px;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
    box-sizing: border-box;
    word-break: break-all;

  }
}
